
'use client';

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Calendar } from '@/components/ui/calendar';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Badge } from '@/components/ui/badge';
import { Utensils, UserCheck, Info } from 'lucide-react';
import { subDays } from 'date-fns';

export default function AttendancePage() {
  const [isClient, setIsClient] = useState(false);
  const [month, setMonth] = useState(new Date());

  useEffect(() => {
    setIsClient(true);
  }, []);

  // Dummy data - easy to replace with API call
  const mealDays = [
    new Date(),
    subDays(new Date(), 1),
    subDays(new Date(), 3),
    subDays(new Date(), 5),
    subDays(new Date(), 8),
    subDays(new Date(), 10),
    subDays(new Date(), 12),
    subDays(new Date(), 15),
    subDays(new Date(), 20),
    subDays(new Date(), 22),
  ];

  const presentDays = [
    new Date(),
    subDays(new Date(), 1),
    subDays(new Date(), 2),
    subDays(new Date(), 4),
    subDays(new Date(), 5),
    subDays(new Date(), 6),
    subDays(new Date(), 7),
    subDays(new Date(), 9),
    subDays(new Date(), 10),
    subDays(new Date(), 11),
    subDays(new Date(), 13),
    subDays(new Date(), 14),
    subDays(new Date(), 16),
  ];

  const absentDays = [
    subDays(new Date(), 3),
    subDays(new Date(), 8),
    subDays(new Date(), 12),
    subDays(new Date(), 15),
  ];

  if (!isClient) {
    // Render a loading state or skeleton to prevent hydration errors
    return (
       <div className="space-y-6 animate-pulse">
        <div className="h-10 w-1/3 bg-muted rounded-md"></div>
        <div className="h-96 bg-muted rounded-xl"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl md:text-4xl font-bold font-headline tracking-tight">Your Attendance</h1>
        <p className="text-muted-foreground">View your meal and mess attendance records.</p>
      </div>

      <Tabs defaultValue="meal-attendance" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="meal-attendance">
            <Utensils className="mr-2 h-4 w-4" /> Meal Attendance
          </TabsTrigger>
          <TabsTrigger value="mess-attendance">
            <UserCheck className="mr-2 h-4 w-4" /> Mess Attendance
          </TabsTrigger>
        </TabsList>
        
        <TabsContent value="meal-attendance" className="mt-4">
          <Card className="bg-card/80 backdrop-blur-sm shadow-xl border-0">
            <CardHeader>
              <CardTitle>Meal Consumption History</CardTitle>
              <CardDescription>
                The calendar below highlights the days you have taken meals in the mess.
                <div className="flex items-center text-xs text-muted-foreground mt-2">
                  <Info className="h-3 w-3 mr-1"/>
                  This data is managed by the mess manager.
                </div>
              </CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col md:flex-row items-center justify-center gap-8 p-6">
              <Calendar
                mode="multiple"
                selected={mealDays}
                onSelect={() => {}} // Read-only
                month={month}
                onMonthChange={setMonth}
                modifiers={{
                    mealTaken: mealDays
                }}
                modifiersStyles={{
                    mealTaken: {
                        color: 'hsl(var(--primary-foreground))',
                        backgroundColor: 'hsl(var(--primary))',
                    }
                }}
                className="rounded-md border p-3 bg-background/50"
              />
              <div className="w-full md:w-auto flex flex-col items-center md:items-start gap-4">
                <div className="text-center md:text-left">
                  <p className="text-4xl font-bold">{mealDays.length}</p>
                  <p className="text-muted-foreground">Total Meals This Month</p>
                </div>
                <div className="flex flex-col gap-2">
                    <h3 className="font-semibold">Legend</h3>
                    <div className="flex items-center gap-2">
                        <Badge className="bg-primary hover:bg-primary text-primary-foreground border-none">Meal Taken</Badge>
                    </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="mess-attendance" className="mt-4">
          <Card className="bg-card/80 backdrop-blur-sm shadow-xl border-0">
            <CardHeader>
              <CardTitle>Mess Presence History</CardTitle>
              <CardDescription>
                The calendar below shows your presence record in the mess.
                <div className="flex items-center text-xs text-muted-foreground mt-2">
                  <Info className="h-3 w-3 mr-1"/>
                  This data is managed by the mess manager.
                </div>
              </CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col md:flex-row items-center justify-center gap-8 p-6">
              <Calendar
                mode="multiple"
                selected={[...presentDays, ...absentDays]}
                onSelect={() => {}} // Read-only
                month={month}
                onMonthChange={setMonth}
                modifiers={{
                  present: presentDays,
                  absent: absentDays,
                }}
                modifiersStyles={{
                  present: {
                    color: 'hsl(var(--primary-foreground))',
                    backgroundColor: 'hsl(140, 70%, 40%)',
                  },
                  absent: {
                    color: 'hsl(var(--destructive-foreground))',
                    backgroundColor: 'hsl(var(--destructive))',
                  },
                }}
                className="rounded-md border p-3 bg-background/50"
              />
               <div className="w-full md:w-auto flex flex-col items-center md:items-start gap-4">
                <div className="grid grid-cols-2 gap-4 text-center md:text-left">
                    <div>
                        <p className="text-4xl font-bold">{presentDays.length}</p>
                        <p className="text-muted-foreground">Present Days</p>
                    </div>
                     <div>
                        <p className="text-4xl font-bold">{absentDays.length}</p>
                        <p className="text-muted-foreground">Absent Days</p>
                    </div>
                </div>
                <div className="flex flex-col gap-2">
                    <h3 className="font-semibold">Legend</h3>
                    <div className="flex items-center gap-2">
                         <Badge className="bg-green-600 hover:bg-green-600 text-primary-foreground border-none">Present</Badge>
                         <Badge variant="destructive">Absent</Badge>
                    </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
